@import 'variables';
@use "sass:math" as math;

@keyframes textGlitch {
    0% {
        transform: skew(0, 0)
    }
    10.3% {
        transform: scaleY(1000%);
        color:#ad1253c5;
    }
    10.9% {
        color:#000000;
    }
    11.2%{
        color:$primaryText;
    }
    11.5% {
        tranfsorm: scaleY(100%);

    }
    12%{
        transform: skewY(-30deg);
        color:#ad1253c5;
    }
    12.5% {
        transform: skewY(0deg);
        color:$primaryText;
    }
    20.5% {
        color:#0bbb22c5;
        text-shadow: 
        0.5rem 0 0 rgba(255, 0, 0, .75),
        -0.5rem -.05rem 0 rgba(0, 0, 255, .75),
        0.5rem 0.3rem 0 rgba(0, 255, 0, .75);
        transform: skewX(-50deg)
    }
    21% {
        color:#61dafb;
        transform: skew(0,0);
        text-shadow: 
        0rem 0 0 rgba(255, 0, 0, .75),
        0rem 0 0 rgba(0, 0, 255, .75),
        0rem 0 0 rgba(0, 255, 0, .75);
    }
    40.5% {
        transform: skewX(50deg)
    }
    41% {
        transform: skew(0,0);
        text-shadow: 
        0.5rem 0 0 rgba(255, 0, 0, .75),
        -0.5rem -.05rem 0 rgba(0, 0, 255, .75),
        0.5rem 0.3rem 0 rgba(0, 255, 0, .75);
    }
    61% {
        color:#ad1253;
        text-shadow: 
        0rem 0 0 rgba(255, 0, 0, .75),
        0rem 0 0 rgba(0, 0, 255, .75),
        0rem 0 0 rgba(0, 255, 0, .75);
    }
    61.5% {
        transform:  skewX(70deg);
        color:$primaryText;
    }
    62% {
        color:#ad1253;
    }
    62.5% {
        transform:  skewX(0deg);
        color:$primaryText;
    }
    81% {
        color:#36ad1248;
    }
    81.4% {
        transform:  skewX(-50deg);
        color:$primaryText;
    }
    81.8% {
        color:#028a205e;
    }
    82.2% {
        transform:  skewX(0deg);
        color:$primaryText;
    }
    100% {
        transform: skew(0,0)
    }
}
@keyframes loading {
    33%{
        color:$primaryText;
    }
}
