@import url('https://fonts.googleapis.com/css2?family=Major+Mono+Display&family=Titillium+Web:wght@200&display=swap');

@import 'variables';
@import 'keyframes';


.loading-screen {
    width:100vw;
    height:100vh;
    font-size:1.25rem;
    display: flex;
    flex-direction: column;
    justify-content: left;
    text-align: left;
}
.hidden {
    display:none;
    visibility: hidden;
}
.title {
    display: block;
    animation: terminal 1 500ms steps(1, end);
}
.line {
    @for $i from 1 through 6 {
        &:nth-of-type(#{$i}) {
          animation: terminal 700ms * ($i - 1 * .2)  steps(1, end);;
        span {
            animation: terminal 850ms * ($i - 1 * .2)  steps(1, end);;
        }
    }}
}
@keyframes terminal {
    0%{
        color: black;
    }
    100% {
        color: $primaryText;
    }
}