
$primaryText: #61dafb;
$sectionBackground: #61dafb71;
$flexDir: row !default;

@mixin flexContainer {
    width:100%;
    height:100%;
    display:flex;
    flex-direction: $flexDir;
    justify-content: space-around;
    align-items: center;
}

@mixin fontsDef {
    font-family: 'Major Mono Display', monospace;
    font-family: 'Titillium Web', sans-serif;
}

@mixin flexList {
    list-style-type: none;
    font-size:15px;
}