
@import url('https://fonts.googleapis.com/css2?family=Major+Mono+Display&family=Titillium+Web:wght@200&display=swap');

@import 'variables';
@import 'keyframes';



* {
  @include fontsDef;
  overflow: hidden;
  color: $primaryText;
}
.App {
    text-align: center;
    width: 100vw;
    height: 100vh;
    background-color: #000;

  }
  
  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 20s linear;
    }
  }
  
  .App-header {
    background-color: #000000;
    min-height: 100vh;
    font-size: calc(10px + 2vmin);
    color:#61dafb;
    @include flexContainer;
  }
  .App-header .ptf{
      animation: textGlitch 7000ms infinite 3000ms steps(1, end);
  }
  
  .App-link {
    color: #61dafb;
  }
  @keyframes skew {
    0% {
      transform: skew(0, 0)
    }

    50% {
      transform: skewY(30deg)
    }
    100% {
      transform: skew(0,0)
    }
  }

  .load {
    position: absolute;
    bottom:10%;
    right:10%;
    color:white;
}
.realc {
    user-select:none;
    position: absolute;
    height:100%;
    letter-spacing: 3px;
    width:100%;
    color:#61dafb77;
    font-size:5px;
    word-wrap: break-word;
    overflow-y: hidden;
}
.code {
    position: absolute;
    top:50%;
    right:50%;
    animation: code 10000ms infinite steps(1, end);
}

.load span:nth-of-type(1) {
    animation: loading 500ms infinite 723ms steps(1, end) 
}
.load span:nth-of-type(2) {
    animation: loading 500ms infinite 1122ms  steps(1, end)
}
.load span:nth-of-type(3) {
    animation: loading 500ms infinite 1333ms steps(1, end)
}

@keyframes loading {
    0% {
        color:white;
    }
    50% {
        color:#000;
    }
    100% {
        color:white;
    }
}

@keyframes code {
    0%{
        visibility:hidden;
    }
    50% {
        visibility: visible;
    }
    55% {
        visibility: hidden;
    }
    60% {
        visibility: visible;
        top:10%;
        right:0%;
    }
    74% {
        visibility: hidden;
    }
    75% {
        visibility: visible;
        height:50%;
        bottom:10%;
        left:20%;
    }
    100% {
        visibility: hidden;
    }
}

.popup {
  position: absolute;
  border:#61dafb 1px solid;
  padding: 2em;
  background-color: #124861b0;
  visibility: hidden;
  animation: one 1000ms 1 6000ms ease-in-out;
  text-transform: uppercase;
  animation: two 2000ms 1 7000ms steps(2, end);
}
.popup p {
  font-size:10px;
}
.popup span{
  animation:loading2 300ms infinite steps(1, end);
}

@keyframes one {
  0%{
    visibility: visible;
    transform: scale(0%);
  }
  10% {
    transform: scaleX(10%);
    transform: scaleY(5%);
  }
  15% {
    transform: scaleX(20%);
    transform: scaleY(10%);
  }
  20% {
    transform: scaleX(40%);
    transform: scaleY(15%);
  }
  25% {
    transform: scaleX(60%);
    transform: scaleY(35%);
  }
  30% {
    transform: scaleX(80%);
    transform: scaleY(50%);
  }
  35% {
    transform: scaleY(40%);
    transform: scaleX(100%);
  }
  70% {
    transform: scaleY(100%);
  }
}
@keyframes two {
  0% {
    visibility: visible;
  }
  10%{
    transform: skewY(-30deg);
  }
  10.001% {
    transform: skewX(100deg);
  }
  10.002% {
    transform: skewY(0deg);
  }
}
.button {
  border-width:none !important; 
  box-shadow: none !important;
  border-radius: none !important;
  border-image: none;
  border-color:none !important;
  cursor:pointer !important;
  background-color: black;
}